@charset "utf-8";

/* button
----------------------------------------------------------------- */

.c-button {
    cursor: pointer;
    &__show-social {
        position: fixed;
        display: flex;
        flex-direction: column;
        width: 50px;
        margin-top: 100px;
        color: $emeraldo;
        &__twitter {
            width: 30px;
            margin: 0 auto;
            margin-top: 50px;
            img {
                background: #f5f5f5;
            }
        }
        &__like {
            width: 50px;
            height: 50px;
            padding: 9px 0;
            margin-bottom: 10px;
            color: #ED4264;
            font-size: 20px;
            text-align: center;
            background: #f5f5f5;
            background-color: #fff;
            box-shadow: 0 3px 6px rgba(0, 0, 0, .2);
            border: 2px solid #ED4264;
            border-radius: 50%;
            outline: none;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
        }
        &__dislike {
            width: 50px;
            height: 50px;
            padding: 9px 0;
            margin-bottom: 10px;
            color: #fff;
            font-size: 20px;
            text-align: center;
            // background: #ff8ba7;
            background: #ED4264;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #FFEDBC, #ED4264);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #FFEDBC, #ED4264); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

            box-shadow: 0 3px 6px rgba(0, 0, 0, .2);
            border-radius: 50%;
            outline: none;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
        }
        &__stock {
            width: 50px;
            height: 50px;
            color: #fff;
            font-size: 20px;
            text-align: center;
            background: #777;
            box-shadow: 0 3px 6px rgba(0, 0, 0, .2);
            border-radius: 50%;
            outline: none;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
            .fa-folder-open {
                padding: 14px 0;
            }
        }
        &__unstock {
            width: 50px;
            height: 50px;
            color: #fff;
            font-size: 20px;
            text-align: center;
            background: rgb(204, 204, 204);
            box-shadow: 0 3px 6px rgba(0, 0, 0, .2);
            border-radius: 50%;
            outline: none;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
            .fa-folder-open {
                padding: 14px 0;
            }
        }
        &__count {
            position: absolute;
            top: -30px;
            width: 50px;
            color: #ff8ba7;
            font-size: 1.3rem;
            font-weight: 600;
            text-align: center;
        }
        &__stockcount {
            position: absolute;
            left: -50px;
            top: 75px;
            width: 50px;
            color: #777;
            font-size: 1.3rem;
            font-weight: 600;
            text-align: center;
        }
    }
    &__show-challange {
        display: block;
        padding: 0.5em 1em;
        text-decoration: none;
        border-radius: 3px;
        font-weight: bold;
        color: #FFF;
        background-image: linear-gradient(45deg, #709dff 0%, #b0c9ff 100%);
        transition: .4s;
        cursor: pointer;
         :hover {
            background-image: linear-gradient(45deg, #709dff 50%, #b0c9ff 100%);
        }
    }
    &-header {
        &__post {
            a {
                padding: .4em 1em;
                color: #fff;
                background: $emeraldo;
                border: 1px solid $emeraldo;
                border-radius: 3px;
                -webkit-border-radius: 3px;
                -moz-border-radius: 3px;
                -ms-border-radius: 3px;
                -o-border-radius: 3px;
            }
        }
    }
}